

import React, { useState } from 'react';

export const Addtodo = (props) => {

    const [title, settitle] = useState ("");
    const [desc, setdesc] = useState ("");
    
    const submit = (e) =>{
      e.preventDefault();
      if(!title || !desc){
        alert("title or desc not be empty");
      }
      else{
      props.addtodo(title, desc);
      settitle("");
      setdesc("");
      }
    }
   

  
    let inputa = {
        background:"none",
        color:"white",
        border:"none",
    }
  return (
    <div className='container my-4'>
      <form onSubmit={submit}>
      <h2 className='head'>ADD TODO</h2>
     
  <div className="form-group">
    <label htmlFor="exampleInputEmail1">title</label>
    <input type="text" className="form-control" id="exampleInputEmail1" style={inputa} aria-describedby="emailHelp" placeholder="Enter title of todo" value={title} onChange={(e)=>{settitle(e.target.value)}}/>
    <small id="emailHelp" className="form-text text-muted"></small>
  </div>
  <div className="form-group">
    <label htmlFor="exampleInputPassword1">your note here</label>
    <input type="text" className="form-control" id="exampleInputPassword1" placeholder="enter your note" style={inputa} value={desc} onChange={(e)=>{setdesc(e.target.value)}}/>
  </div>

  <button type="submit" className="btn btn-danger btn-sm">Submit</button>
  </form>
    </div>
  )
}

