import React from 'react'

const Todoitems = ({todos , ondelete}) => {
  let inputa = {
    background:"none",
    color:"white",
    border:"none",
}
  return (
    <div className=' container text-light'>
      <h4 >{todos.title}</h4>
      <p >{todos.desc}</p>
      <button className="btn btn-sm btn-danger" onClick={() => {ondelete(todos)}}>DELETE</button>
      <hr/>
    </div>
  )
}

export default Todoitems
