import React from 'react'
import PropTypes from 'prop-types'
import {
  Link
} from "react-router-dom";



export default function header(props) {
  let nav = {
    filter: "blur(.6px)",  
    color: "white",
}
  let color = {  
    color: "white",
}
  return (
    <div>
   <link rel="stylesheet" href="style.css" />
  <nav className={` navbar navbar-expand-lg navbar-${props.mode}    ` }style={nav}>
  <Link className={`navbar-brand text-light`} to="#">{props.title}</Link>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

  <div className="collapse navbar-collapse" id="navbarSupportedContent" >
    <ul className="navbar-nav mr-auto " style={color}>
      <li className="nav-item active">
        <Link className={`nav-link text-light text-light' `} to="/">Home<span className="sr-only">(current)</span></Link>
      </li>
      <li className="nav-item active">
        <Link className={`nav-link text-light text-light' `} to="/showtodo">show todos <span className="sr-only">(current)</span></Link>
      </li>
      <li className="nav-item active">
        <Link className={`nav-link text-light text-light `} to="/">about <span className="sr-only">(current)</span></Link>
      </li>

    </ul>

    <div className={`custom-control custom-switch text-light`} >
  <input type="checkbox" onClick={props.toggle} className="custom-control-input" id="customSwitch1"/>
  <label className="custom-control-label" htmlFor="customSwitch1">{`Enable ${props.mode==='light'?'dark':'light'} mode`}</label>
</div>
  </div>
</nav>

    </div>
  )
}

header.defaultProps = {
    title:"title here",
    search: true
}
Headers.PropTypes = {
    title: PropTypes.string
}