import React from 'react'
import Todoitem from '../Mycomponents/Todoitems'

const Todos = ({todos , ondelete}) => {
  return (
<div className='container'>
    <h3 className=' '>TODOS - LIST</h3>
    {todos.length===0? "no todos to dispaly sorry for that" : 
    todos.map((todos)=>{
    return <Todoitem todos={todos} key={todos.srno} ondelete={ondelete}/>   
  
   })
   }
    
</div>
  )
}

export default Todos

