
import './App.css';
import Header from "./Mycomponents/Header";
import Bgimg from "./Mycomponents/bg.jpg"
import Bgimg2 from "./Mycomponents/bg2.png"
import Footer from "./Mycomponents/Footer";
import Todos from "./Mycomponents/Todos";
import React, { useState, useEffect } from 'react';
import { Addtodo } from "./Mycomponents/Addtodo"
import About from "./Mycomponents/About"
import {
  BrowserRouter as Router,
  Switch,
  Routes,
  Route,

} from "react-router-dom";

function App() {
  let inittodo;
  if (localStorage.getItem("todos") === null) {
    inittodo = []
  }
  else {
    inittodo = JSON.parse(localStorage.getItem("todos"));
  }
  const ondelete = (todo) => {
    // console.log("hello for todos ", todo);
    setTodos(todos.filter((e) => {
      return e !== todo;
    }));
    localStorage.setItem("todos", JSON.stringify(todos));
  }

  const addtodo = (title, desc) => {
    let sno;
    if (todos.lenght === 0) {
      sno = 0;
    }
    else {

      sno = todos[todos.length - 1] + 1;
    }

    const mytodos =
    {
      sno: sno,
      title: title,
      desc: desc,
    }
    setTodos([...todos, mytodos]);

  }
  const [todos, setTodos] = useState(inittodo)
  useEffect(() => {
    localStorage.setItem("todos", JSON.stringify(todos));
  }, [todos])


  const [mode, setDarkMode] = useState('dark');
  const toggle = () => {
    if(mode === 'light' ){
      setDarkMode('dark');
      document.body.style. backgroundImage = `url(${Bgimg})`;
    }
    else{
      setDarkMode('light');
      document.body.style. backgroundImage = `url(${Bgimg2})`;
    }
    
  }
  return (
    <>
      <Router>
       <Header title="My Todos List" serach={true} mode={mode} toggle={toggle}/>
    
       <Routes>
          			<Route exact path="/" element={<Addtodo addtodo={addtodo} />
                } 
                />
          			<Route exact path="/showtodo" element={
                  <Todos todos={todos} ondelete={ondelete}/>
                } />
          			<Route exact path="/about" element={
                  <About/>
                } />
        		</Routes>

      
       <Footer />

      </Router>
    </>
  );
}

export default App;
